body {
}
body {margin:0;padding:0;}
ul, li {margin:0;padding:0;list-style:none;}
h1,h2,h3,h4,h5 {margin:0;padding:0;}

section {
}

.header {
    width:1200px;
    margin:0 auto;
    h1 {
        display:inline-block;
    }
    ul {
        display:inline-block;
        float:right;
        li {
            display:inline-block;
            
        }
    }
}

#wrap {
    width:1200px;
    margin:0 auto;
    padding-top:6rem;
    .container {
        display:table;
        width: 100%;
        div {
            display:table-cell;
            &.wrap_left {
                color:#fff;
                h2 {
                    font-size:50px;
                    font-weight:700;
                }
                ul {
                    li {
                        &::before {
                            content:'ss';
                        }
                    }
                }
            }
            &.wrap_right {
                width:350px;
                background:#fff;
                padding:8rem 3rem;
                h3 {
                    font-size:30px;
                    font-weight:700;
                    margin-bottom:5px;
                }
                h4 {
                    color:#999999;
                    font-weight:300;
                    margin-bottom:30px;
                }
                input {
                    width:100%;
                    padding:20px;
                    border:none;
                    background:#f3f3f3;
                    box-sizing : border-box;
                    &[type="text"] {
                        margin-bottom:10px;
                    }
                }
                p {
                    font-size:14px;
                    color:#0066d0;
                    text-align:center;
                }
                button {
                    width:100%;
                    border:none;
                    background:#0066d0;
                    color:#fff;
                    padding:20px;
                    border-radius: 30px;
                    margin-top:40px;
                }
           }
        }
    }
   
}